<template lang="pug">
  nav(aria-label="Page navigation")
    .buttons.has-addons
      a.button.is-light.is-small(
        :disabled="pagination.current_page == 1 ? true:false",
        href="#!", 
        aria-label="Previous"
        @click="nextPrev($event, pagination.current_page-1)"
      )
        span(aria-hidden="true") «
      a.button.is-small(
        @click="navigate($event, page)",
        v-for="(page,index) in pages",
        :key="index",
        track-by="$index"
        :class="pagination.current_page==page?'is-primary':'is-light'"
        )
        
        span(v-if="page == '...'") {{page}}
        a(href="#!" v-if="page != '...'") {{page}}
      a.button.is-light.is-small(
        :disabled="pagination.current_page == pagination.last_page",
        href="#!", aria-label="Next"
        @click="nextPrev($event, pagination.current_page+1)"
      )
        span(aria-hidden="true") »
</template>

<script>
export default {
  name: "paginators",
  props: ["pagination"],
  data() {
    return {
      pages: []
    };
  },
  ready() {},
  created() {
    //console.log(this.pagination); //está trayendo pagination
    let p = this.pagination;
    this.pages = this.generatePagesArray(
      p.current_page,
      p.total,
      p.per_page,
      7
    );
  },
  methods: {
    navigate(ev, page) {
      ev.preventDefault();
      //console.log(page);
      this.$emit("navigate", page);
    },
    nextPrev(ev, page) {
      if (page == 0 || page == this.pagination.last_page + 1) {
        return;
      }
      this.navigate(ev, page);
    },
    generatePagesArray(
      currentPage,
      collectionLength,
      rowsPerPage,
      paginationRange
    ) {
      var pages = [];
      var totalPages = Math.ceil(collectionLength / rowsPerPage);
      var halfWay = Math.ceil(paginationRange / 2);
      var position;
      if (currentPage <= halfWay) {
        position = "start";
      } else if (totalPages - halfWay < currentPage) {
        position = "end";
      } else {
        position = "middle";
      }
      var ellipsesNeeded = paginationRange < totalPages;
      var i = 1;
      while (i <= totalPages && i <= paginationRange) {
        var pageNumber = this.calculatePageNumber(
          i,
          currentPage,
          paginationRange,
          totalPages
        );
        var openingEllipsesNeeded =
          i === 2 && (position === "middle" || position === "end");
        var closingEllipsesNeeded =
          i === paginationRange - 1 &&
          (position === "middle" || position === "start");
        if (
          ellipsesNeeded &&
          (openingEllipsesNeeded || closingEllipsesNeeded)
        ) {
          pages.push("...");
        } else {
          pages.push(pageNumber);
        }
        i++;
      }
      return pages;
    },
    calculatePageNumber(i, currentPage, paginationRange, totalPages) {
      var halfWay = Math.ceil(paginationRange / 2);
      if (i === paginationRange) {
        return totalPages;
      } else if (i === 1) {
        return i;
      } else if (paginationRange < totalPages) {
        if (totalPages - halfWay < currentPage) {
          return totalPages - paginationRange + i;
        } else if (halfWay < currentPage) {
          return currentPage - halfWay + i;
        } else {
          return i;
        }
      } else {
        return i;
      }
    }
  },

};
</script>

<style scoped>
</style>


